import React, { useState, useEffect } from "react";
import { Plus, Trash2, Save, Loader2, X } from "lucide-react";
import { Button } from "../common/Button";
import api from "../../services/api";

interface TypeProjet {
  idType: string;
  Libelle: string;
  Description: string;
}

interface ProcessStep {
  idEtape: string;
  LibelleEtape: string;
  Description: string;
  Validation: string;
  sequenceNumber: number;
  roleId: string | null;
  TypeProjets: TypeProjet[];
}

export const ProcessStepsManagement: React.FC = () => {
  const [steps, setSteps] = useState<ProcessStep[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showModal, setShowModal] = useState(false); // État pour afficher/masquer la modale
  const [newStep, setNewStep] = useState<ProcessStep>({
    idEtape: "",
    LibelleEtape: "",
    Description: "",
    Validation: "",
    sequenceNumber: steps.length + 1,
    roleId: null,
    TypeProjets: [],
  });

  useEffect(() => {
    const fetchSteps = async () => {
      try {
        const response = await api.get("/etapes/all");
        setSteps(response.data);
      } catch (err) {
        setError("Erreur lors du chargement des étapes");
        console.error(err);
      } finally {
        setIsLoading(false);
      }
    };

    fetchSteps();
  }, []);

  const addStep = () => {
    console.log("Avant mise à jour showModal:", showModal); // Vérifiez l'état avant
    setShowModal(true); // Afficher la modale
    console.log("Après mise à jour showModal:", showModal); // Vérifiez l'état après
  };

  const handleModalClose = () => {
    setShowModal(false); // Masquer la modale
    setNewStep({
      idEtape: "",
      LibelleEtape: "",
      Description: "",
      Validation: "",
      sequenceNumber: steps.length + 1,
      roleId: null,
      TypeProjets: [],
    }); // Réinitialiser le formulaire
  };

  const handleModalSave = () => {
    if (!newStep.LibelleEtape.trim() || !newStep.Description.trim()) {
      alert("Veuillez remplir tous les champs obligatoires.");
      return;
    }

    setSteps([
      ...steps,
      {
        ...newStep,
        idEtape: Date.now().toString(), // Générer un ID unique
        sequenceNumber: steps.length + 1,
      },
    ]);
    handleModalClose(); // Fermer la modale après l'ajout
  };

  const removeStep = (id: string) => {
    setSteps(steps.filter((s) => s.idEtape !== id));
  };

  const updateStep = (
    id: string,
    field: keyof ProcessStep,
    value: string | number | null
  ) => {
    setSteps(
      steps.map((s) => (s.idEtape === id ? { ...s, [field]: value } : s))
    );
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      await api.post("/etapes/update", { steps });
      alert("Étapes mises à jour avec succès !");
    } catch (err) {
      console.error("Erreur lors de la mise à jour des étapes :", err);
      alert("Erreur lors de la mise à jour des étapes.");
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900">
          Configuration des étapes
        </h1>
        <Button variant="primary" icon={Plus} onClick={addStep}>
          Ajouter une étape
        </Button>
      </div>

      {isLoading ? (
        <div className="flex justify-center py-8">
          <Loader2 className="animate-spin w-8 h-8 text-blue-500" />
        </div>
      ) : error ? (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative">
          {error}
        </div>
      ) : (
        Array.isArray(steps) && (
          <div className="overflow-x-auto">
            <table className="min-w-full bg-white">
              <thead>
                <tr>
                  <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                    Nom
                  </th>
                  <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                    Description
                  </th>
                  <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                    Ordre
                  </th>
                  <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                    Rôle requis
                  </th>
                  <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody>
                {steps.map((step) => (
                  <tr key={step.idEtape} className="hover:bg-gray-50">
                    <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                      {step.LibelleEtape}
                    </td>
                    <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                      {step.Description}
                    </td>
                    <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                      {step.sequenceNumber}
                    </td>
                    <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                      {step.roleId || "N/A"}
                    </td>
                    <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                      <button
                        type="button"
                        onClick={() => removeStep(step.idEtape)}
                        className="text-red-600 hover:text-red-700"
                      >
                        <Trash2 className="h-5 w-5" />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )
      )}

      <form onSubmit={handleSubmit}>
        <div className="mt-6 flex justify-end">
          <Button type="submit" variant="primary" icon={Save}>
            Enregistrer les modifications
          </Button>
        </div>
      </form>

      {/* Modale pour ajouter une étape */}
      {showModal &&
        (console.log("Affichage de la modale"), // Vérifiez si cette ligne est atteinte
        (
          <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
            <div className="bg-white rounded-lg shadow-lg p-6 w-full max-w-lg">
              <div className="flex justify-between items-center mb-4">
                <h2 className="text-xl font-bold">Ajouter une étape</h2>
                <button
                  onClick={handleModalClose}
                  className="text-gray-400 hover:text-gray-600"
                >
                  <X className="h-5 w-5" />
                </button>
              </div>
              <div className="space-y-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Nom de l'étape
                  </label>
                  <input
                    type="text"
                    value={newStep.LibelleEtape}
                    onChange={(e) =>
                      setNewStep({ ...newStep, LibelleEtape: e.target.value })
                    }
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Description
                  </label>
                  <textarea
                    value={newStep.Description}
                    onChange={(e) =>
                      setNewStep({ ...newStep, Description: e.target.value })
                    }
                    rows={3}
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                    required
                  />
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700">
                    Rôle requis
                  </label>
                  <input
                    type="text"
                    value={newStep.roleId || ""}
                    onChange={(e) =>
                      setNewStep({ ...newStep, roleId: e.target.value || null })
                    }
                    className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                  />
                </div>
              </div>
              <div className="mt-6 flex justify-end space-x-2">
                <Button variant="secondary" onClick={handleModalClose}>
                  Annuler
                </Button>
                <Button variant="primary" onClick={handleModalSave}>
                  Ajouter
                </Button>
              </div>
            </div>
          </div>
        ))}
    </div>
  );
};
