import React, { useState, useEffect } from "react";
import { Shield, Plus, Trash2, Save, Loader2 } from "lucide-react";
import { Card } from "../common/Card";
import { Button } from "../common/Button";
import api from "../../services/api";

interface Permission {
  id: string;
  name: string;
  description: string;
}

interface Role {
  idRole: string;
  name: string;
  description: string;
  isSystemRole: boolean;
  permissions: string[] | null;
  createdAt: string;
  updatedAt: string;
}

const availablePermissions: Permission[] = [
  {
    id: "create_process",
    name: "Créer un processus",
    description: "Permet de créer de nouveaux processus",
  },
  {
    id: "edit_process",
    name: "Modifier un processus",
    description: "Permet de modifier les processus existants",
  },
  {
    id: "delete_process",
    name: "Supprimer un processus",
    description: "Permet de supprimer des processus",
  },
  {
    id: "approve_step",
    name: "Approuver une étape",
    description: "Permet d'approuver les étapes d'un processus",
  },
  {
    id: "reject_step",
    name: "Rejeter une étape",
    description: "Permet de rejeter les étapes d'un processus",
  },
  {
    id: "manage_users",
    name: "Gérer les utilisateurs",
    description: "Permet de gérer les utilisateurs",
  },
  {
    id: "view_reports",
    name: "Voir les rapports",
    description: "Permet de consulter les rapports",
  },
];

export const RolesManagement: React.FC = () => {
  const [roles, setRoles] = useState<Role[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchRoles = async () => {
      try {
        const response = await api.get("/rolesss"); // Route pour récupérer tous les rôles
        setRoles(response.data);
      } catch (err) {
        setError("Erreur lors du chargement des rôles");
        console.error(err);
      } finally {
        setIsLoading(false);
      }
    };

    fetchRoles();
  }, []);

  const addRole = () => {
    setRoles([
      ...roles,
      {
        idRole: Date.now().toString(),
        name: "",
        description: "",
        isSystemRole: false,
        permissions: [],
        createdAt: new Date().toISOString(),
        updatedAt: new Date().toISOString(),
      },
    ]);
  };

  const removeRole = (idRole: string) => {
    setRoles(roles.filter((r) => r.idRole !== idRole));
  };

  const updateRole = (
    idRole: string,
    field: keyof Role,
    value: string | string[] | boolean
  ) => {
    setRoles(
      roles.map((r) => (r.idRole === idRole ? { ...r, [field]: value } : r))
    );
  };

  const togglePermission = (roleId: string, permissionId: string) => {
    const role = roles.find((r) => r.idRole === roleId);
    if (!role) return;

    const newPermissions = role.permissions?.includes(permissionId)
      ? role.permissions.filter((p) => p !== permissionId)
      : [...(role.permissions || []), permissionId];

    updateRole(roleId, "permissions", newPermissions);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    // TODO: Implement roles update
    console.log("Roles:", roles);
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold text-gray-900">Gestion des rôles</h1>
        <Button variant="primary" icon={Plus} onClick={addRole}>
          Ajouter un rôle
        </Button>
      </div>

      {isLoading ? (
        <div className="flex justify-center py-8">
          <Loader2 className="animate-spin w-8 h-8 text-blue-500" />
        </div>
      ) : error ? (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded relative">
          {error}
        </div>
      ) : (
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead>
              <tr>
                <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                  Nom
                </th>
                <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                  Description
                </th>
                <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                  Rôle Système
                </th>
                <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                  Permissions
                </th>
                <th className="px-6 py-3 border-b-2 border-gray-300 text-left text-sm font-semibold text-gray-700">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody>
              {roles.map((role) => (
                <tr key={role.idRole} className="hover:bg-gray-50">
                  <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                    {role.name}
                  </td>
                  <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                    {role.description}
                  </td>
                  <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                    {role.isSystemRole ? "Oui" : "Non"}
                  </td>
                  <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                    {role.permissions ? role.permissions.join(", ") : "Aucune"}
                  </td>
                  <td className="px-6 py-4 border-b border-gray-200 text-sm text-gray-900">
                    <button
                      type="button"
                      onClick={() => removeRole(role.idRole)}
                      className="text-red-600 hover:text-red-700"
                    >
                      <Trash2 className="h-5 w-5" />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}

      <form onSubmit={handleSubmit}>
        <div className="space-y-4">
          {roles.map((role) => (
            <Card key={role.idRole}>
              <div className="space-y-4">
                <div className="flex items-center justify-between">
                  <div className="flex items-center space-x-3">
                    <div className="p-2 bg-blue-100 rounded-lg">
                      <Shield className="h-5 w-5 text-blue-600" />
                    </div>
                    <h3 className="text-lg font-medium text-gray-900">
                      {role.name || "Nouveau rôle"}
                    </h3>
                  </div>
                  <button
                    type="button"
                    onClick={() => removeRole(role.idRole)}
                    className="text-red-600 hover:text-red-700"
                  >
                    <Trash2 className="h-5 w-5" />
                  </button>
                </div>

                <div className="grid grid-cols-1 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700">
                      Nom du rôle
                    </label>
                    <input
                      type="text"
                      value={role.name}
                      onChange={(e) =>
                        updateRole(role.idRole, "name", e.target.value)
                      }
                      className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700">
                      Description
                    </label>
                    <textarea
                      value={role.description}
                      onChange={(e) =>
                        updateRole(role.idRole, "description", e.target.value)
                      }
                      rows={2}
                      className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-blue-500 focus:ring-blue-500 sm:text-sm"
                      required
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Permissions
                    </label>
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                      {availablePermissions.map((permission) => (
                        <div key={permission.id} className="flex items-start">
                          <div className="flex items-center h-5">
                            <input
                              type="checkbox"
                              checked={
                                role.permissions?.includes(permission.id) ||
                                false
                              }
                              onChange={() =>
                                togglePermission(role.idRole, permission.id)
                              }
                              className="h-4 w-4 text-blue-600 focus:ring-blue-500 border-gray-300 rounded"
                            />
                          </div>
                          <div className="ml-3 text-sm">
                            <label className="font-medium text-gray-700">
                              {permission.name}
                            </label>
                            <p className="text-gray-500">
                              {permission.description}
                            </p>
                          </div>
                        </div>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </Card>
          ))}
        </div>

        {roles.length > 0 && (
          <div className="mt-6 flex justify-end">
            <Button type="submit" variant="primary" icon={Save}>
              Enregistrer les modifications
            </Button>
          </div>
        )}
      </form>
    </div>
  );
};
